  }
}

.subtitle {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
}
.subtitle--bold {
  font-weight: 700;
}
.subtitle--uppercase {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .main-container__wrapper {
