.main-header {
  position: relative;
  min-height: 56px;
}

.main-header__logo {
  position: absolute;
  top: 0;
  left: 50%;
  z-index: 1;
  width: 185px;
  height: 96px;
  transform: translate(-50%, 0);
}
@media (max-width: 767px) {
  .no-js .main-header__logo {
    z-index: -1;
  }
}
.main-header__logo:hover {
  opacity: 95%;
}

.main-header__toggle {
  content: "";
  position: absolute;
  top: 17px;
  right: 20px;
  z-index: 3;
  width: 23px;
  height: 22px;
  overflow: visible;
  background-color: #ffffff;
  border: none;
  cursor: pointer;
}
.main-header__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 23px;
  height: 4px;
  background-color: #81b3d2;
  box-shadow: 0 9px 0 0 #81b3d2, 0 18px 0 0 #81b3d2;
}
.main-header__toggle:hover::before {
  background-color: rgba(129, 179, 210, 0.6);
  box-shadow: 0 9px 0 0 rgba(129, 179, 210, 0.6), 0 18px 0 0 rgba(129, 179, 210, 0.6);
}
.main-header__toggle:active::before {
  background-color: rgba(129, 179, 210, 0.3);
  box-shadow: 0 9px 0 0 rgba(129, 179, 210, 0.3), 0 18px 0 0 rgba(129, 179, 210, 0.3);
}
.no-js .main-header__toggle {
  display: none;
}
.main-header__toggle--is-opened {
  background-color: transparent;
}
.main-header__toggle--is-opened::before, .main-header__toggle--is-opened::after {
  content: "";
  position: absolute;
  top: 9px;
  left: 0;
  width: 23px;
  height: 4px;
  background-color: #ffffff;
}
.main-header__toggle--is-opened::before {
  box-shadow: none;
  transform: rotate(45deg);
}
.main-header__toggle--is-opened::after {
  box-shadow: none;
  transform: rotate(-45deg);
}
.main-header__toggle--is-opened:hover::before, .main-header__toggle--is-opened:hover::after {
  background-color: rgba(255, 255, 255, 0.6);
}
.main-header__toggle--is-opened:active::before, .main-header__toggle--is-opened:active::after {
  background-color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 768px) {
  .main-header__toggle--is-opened {
    display: none;
  }
}
@media (min-width: 768px) {
  .main-header__toggle {
    display: none;
  }
}

.navigation__list {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  display: none;
  width: 100%;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (min-width: 768px) {
  .navigation__list {
    position: relative;
    z-index: 0;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
  }
  .navigation__list::after {
    content: "";
    grid-column: 3/4;
    grid-row: 1/3;
  }
}
@media (max-width: 767px) {
  .navigation__list--is-opened {
    display: block;
  }
}
