.form__radio,
.form__checkbox {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.form__icon {
  position: relative;
}
.form__icon::before {
  content: "";
  display: grid;
  align-self: start;
  width: 46px;
  height: 46px;
  background-color: #f2f2f2;
  background-position: center center;
  background-size: 50% 50%;
}
@media (min-width: 768px) {
  .form__icon::before {
    margin-right: 16px;
  }
}
.form__icon--radio::before {
  border-radius: 50%;
}
.form__icon--checkbox::before {
  border-radius: 4px;
}

.form__radio:hover + .form__icon::before,
.form__checkbox:hover + .form__icon::before {
  background-color: #e5e5e5;
}

.form__radio:checked + .form__icon::before,
.form__checkbox:checked + .form__icon::before {
  background-color: #f2f2f2;
}

.form__radio:checked:hover + .form__icon::before,
.form__checkbox:checked:hover + .form__icon::before {
  background-color: #e5e5e5;
}

.form__radio:focus + .form__icon::before,
.form__checkbox:focus + .form__icon::before {
  background-color: #d5d5d5;
}

.form__radio:checked:focus + .form__icon::before,
.form__checkbox:checked:focus + .form__icon::before {
  background-color: #d5d5d5;
}

.form__radio:checked + .form__icon::after {
  content: "";
  position: absolute;
  top: 15px;
  left: 15px;
  display: inline-block;
  width: 16px;
  height: 16px;
  background-color: #333333;
  border-radius: 50%;
}

.form__radio:focus:checked + .form__icon::after {
  background-color: rgba(51, 51, 51, 0.3);
}

.form__checkbox:checked + .form__icon::after {
  content: "";
  position: absolute;
  top: 15px;
  left: 12px;
  display: inline-block;
  width: 22px;
  height: 16px;
  background: url("../img/stack.svg#tick");
}

.form__checkbox:focus:checked + .form__icon::after {
  opacity: 30%;
}

